/* cairo-200 - latin */
@font-face {
    font-family: 'Cairo';
    font-style: normal;
    font-weight: 200;
    src: local(''),
         url('../fonts/cairo-v17-latin-200.woff2') format('woff2'), /* Chrome 26+, Opera 23+, Firefox 39+ */
         url('../fonts/cairo-v17-latin-200.woff') format('woff'); /* Chrome 6+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
